import React from 'react'
import { Button, ButtonGroup, ButtonToolbar } from "react-bootstrap";
import { cars } from "../ModelOverview/data";
function Filter({setAllCars}) {
    return (
        <div className='my-4'>
            <ButtonToolbar aria-label="Toolbar with button groups">
                <ButtonGroup className='me-2 mt-1' aria-label="First group">
                    <Button onClick={() => setAllCars(cars[0].cars)} variant="dark">A3</Button>
                    <Button onClick={() => setAllCars(cars[1].cars)} variant="dark">A4</Button>
                    <Button onClick={() => setAllCars(cars[2].cars)} variant="dark">A5</Button>
                    <Button onClick={() => setAllCars(cars[3].cars)} variant="dark">A6</Button>
                    <Button onClick={() => setAllCars(cars[4].cars)} variant="dark">A7</Button>
                </ButtonGroup>
                <ButtonGroup className='me-2 mt-1' aria-label="Second group">
                    <Button onClick={() => setAllCars(cars[5].cars)} variant="dark">Sedan</Button>
                    <Button onClick={() => setAllCars(cars[6].cars)} variant="dark">SUV</Button>
                    <Button onClick={() => setAllCars(cars[7].cars)} variant="dark">Sportback</Button>
                </ButtonGroup>
            </ButtonToolbar>
           
        </div>
    )
}

export default Filter;
