import React from 'react'
import { Button } from "react-bootstrap";
const DesiredAudi = () => {
    return (
        <div className='text-center' style={{margin : "100px 0px 115px 0px"}}>
            <h1>Looking for your desired Audi?</h1>
            <p className='mt-4'>Discover all available Audi models here.</p>
            <Button variant='secondary' className='mt-4' href='/view-all'>All models</Button>
        </div>
    )
}

export default DesiredAudi;
