import React from 'react'
function ModelCarContents({headingText, text, img}) {
    return (
        <div className="mt-4 mb-5 d-flex flex-column justify-content-center">
                <div className='mb-3 text-center'>
                <h2>{headingText}</h2>
                <p>{text}</p>
                </div>
                <div className=''>
                <img src={img}
                 alt="audi"
                 className='img-fluid'
                 />
                </div>
            </div>
    )
}

export default ModelCarContents;
