import React from 'react'
import { Navbar, Nav, Container } from 'react-bootstrap';
const NavBar = () => {
    return (
        <div>
            <Navbar bg="light" expand="lg">
            <Container>
              <Navbar.Brand href="/audi-website">
              <img src='https://www.carlogos.org/logo/Audi-logo-1999-1920x1080.png' style={{height: "45px", width:"87px"}} alt='logo' />
              </Navbar.Brand>
              <Navbar.Toggle aria-controls="basic-navbar-nav" />
              <Navbar.Collapse id="basic-navbar-nav">
                <Nav className="me-auto">
                  <Nav.Link href="/audi-website/#/view-all">All models</Nav.Link>
                  <Nav.Link href="/audi-website/#/about">About us</Nav.Link>
                  <Nav.Link href="/audi-website/#/arrange-test-drive">Arrange a test drive</Nav.Link>
                </Nav>
              </Navbar.Collapse>
            </Container>
          </Navbar>
        </div>
    )
}

export default NavBar;
