import React from 'react'
import { Button } from 'react-bootstrap';

function Contents() {
    return (
        <div>
            <div className='row' style={{ margin: "50px"}}>
                <img src='https://www.audi.com.my/content/dam/nemo/sea/my/Promotions/2021/AOSMY_highlightedcar_883x883.jpg'
                alt='audiCars'
                className='col-lg-7 col-md-12'
                />
                <div className='col-lg-5 mt-3'>
                    <h2>The Audi Online Showroom</h2>
                    <p>Introducing the online showroom, where you can discover our full line-up of models right from your home or wherever you are. For the first time ever in Malaysia, new models like the Q3 Sportback and RS models such as the RS 4 Avant, RS 5 Sportback, RS 6 Avant and RS 7 Sportback will be available for bookings.</p>
                    <Button size="lg" variant='dark'>Register and discover offers now</Button>
                </div>
            </div>
            <div className='row' style={{ margin: "50px"}}>
                <img src='https://www.audi.com.my/content/dam/nemo/sea/my/Promotions/2021/A5_highlightedcar_883x883.jpg'
                alt='audiA5'
                className='col-lg-7'
                />
                <div className='col-lg-5 mt-3'>
                    <h2>The new Audi A5 Sportback</h2>
                    <p>Featuring the emotional design of a coupé, the performance of a sedan and the functionality of an Avant, the new Audi A5 Sportback is the perfect mid-size Audi for you.</p>
                    <Button size="lg" variant='dark'>Discover Now</Button>
                </div>
            </div>
        </div>
    )
        
        
}

export default Contents;
