import React from 'react'
import { Button } from 'react-bootstrap';
const Header = () => {
    return (
        <div style={{
            backgroundImage : 'url(https://www.audi.com.my/content/dam/nemo/sea/my/Promotions/2021/Q3range_1920x1080.jpg?downsize=1440px:*)',
            height: "600px",
            color: 'white',
            position: "relative"
            }}>
            <div style={{position: "absolute", bottom: "35px", left: "115px"}}>
                <div>
                    <h2>
                        Sharper. Smarter. More spacious.
                    </h2>
                    <p>
                        The new Audi Avant.
                    </p>
                </div>
                <div>
                    <Button href="/rs4-avant" className='m-1' variant='light' size="lg" style={{color: "white", marginRight: "5px", background: "transparent"}}> Audi RS 4 </Button>
                    <Button href="/rs6-avant" className='m-1' variant='light' size="lg"> Audi RS 6 Avant </Button>
                </div>
            </div>
      </div>

    )
}

export default Header;