import React from 'react'

const ModelHeader = ({bgImage, model}) => {
    return (
        <div style={{
            backgroundImage : `url(${bgImage})`,
            height: "400px",
            color: 'white',
            backgroundPosition: "center",
            backgroundSize: "cover",
            marginBottom: "50px"
            }}
            >
            <div className='row'>
                <h2 className='m-4'>{model}</h2>
            </div>
        </div>
    )
}

export default ModelHeader;
