import React from 'react';
import { Button } from 'react-bootstrap';
function CarModels({car}) {
    return (
            <div className='col-lg-4 col-md-6 col-sm-12 d-flex flex-column'>
                <h3 className='mt-4'>{car.model}</h3>
                <img src={car.img} 
                style={{height: "160px", width: "350px"}}
                alt='audi'
                className='align-self-center'
                />
                <Button variant='outline-dark' size="lg" href={car.url}>Information</Button>
            </div>
    )
}

export default CarModels;
