import React,{ useState } from 'react'
import { Container } from "react-bootstrap";
import { modelOverviewModels, variantsModelOverview, cars } from './data';
import { CloseButton, Button } from 'react-bootstrap';
const ModelOverview = () => {
    const [model, setModelNVariant] = useState( modelOverviewModels );
    const [models, setModels] = useState('');

    const setCarModels = () => {
        if(model !== modelOverviewModels){
            setModelNVariant( modelOverviewModels );
            setModels('');
        };
    };

    const setCarVariants = () => {
        if(model !== variantsModelOverview){
            setModelNVariant( variantsModelOverview );
            setModels('');
        };
    };

    return (
        <Container>
            <h2 style={{marginTop: "25px"}}>Model Overview</h2>
                <div className='d-flex mt-4'>
                    <Button variant='dark' onClick={setCarModels}>Models</Button> 
                    <Button variant='secondary' onClick={setCarVariants} style={{marginLeft:"10px"}}>Variants</Button> 
                </div>
                <div className='row d-flex justify-content-center mt-5'>
                    {
                        model.map((model) => (
                            <div className='col-lg-2 col-sm-12 d-flex flex-column justify-content-center' key={model.id}
                            style={{ margin: "0px 15px 0 15px", cursor: "pointer"}}
                            onClick={() => {
                                setModels(cars.find(car => car.model === model.model));
                            }}
                            >
                                <img 
                                src={model.img} 
                                style={{height: "100px", width: "228px"}}
                                alt='model'
                                className='align-self-center'
                                /> 
                                <h5 style={{textAlign: "center"}}>{model.model}</h5>   
                            </div>  
                        ))
                    }

                        { 
                            models ?
                            <div className='row d-flex flex-column'>
                            <CloseButton 
                            className='m-3 d-block align-self-center text-center' 
                            style={{height: "35px", width: "35px", border: "3px solid red", borderRadius: "30px"}}
                            onClick={() => setModels('')}
                            />
                            </div> :
                            null
                        }

                    
                        <div className='row'>
                            {
                            models ?  models.cars.map( car => (
                                    <a href={car.url} className='text-decoration-none text-dark col d-flex flex-column justify-content-center mt-3' style={{cursor: "pointer"}}>
                                        <h5 style={{textAlign: "center"}}>{car.model}</h5>
                                        <img 
                                        src={car.img} 
                                        style={{height: "100px", width: "228px" }}
                                        alt='model'
                                        className='align-self-center'
                                        /> 
                                    </a>
                            )) : null
                           }
                           </div>
                    </div>
                             
        </Container>
    )
}

export default ModelOverview;
