import React from 'react'
import { Button, Container, Form } from 'react-bootstrap';

function ArrangeTestDrive() {
    return (
        <Container className='my-2'>
            <Form>
                <h2>Arrange a test drive</h2>
                <Form.Select size="sm" className='my-4' required>
                    <option>Desired Model</option>
                    <option>A3 Sedan</option>
                    <option>A4 Sedan</option>
                    <option>RS4 Avant</option>
                    <option>A5 Sportback</option>
                    <option>RS5 Sportback</option>
                    <option>A6 Sedan</option>
                    <option>RS6 Avant</option>
                    <option>A7 Sportback</option>
                    <option>RS7 Sportback</option>
                </Form.Select>
                <div className="row">
                    <div className='col-lg-6 col-sm-12 mt-2'>
                    <Form.Label>
                        First name
                    </Form.Label>
                    <Form.Control type="text" />
                    </div>
                    <div className='col-lg-6 col-sm-12 mt-2'>
                    <Form.Label>
                        Last name
                    </Form.Label>
                    <Form.Control type="text" />
                    </div>
                    <div className='col-lg-6 col-sm-12 mt-2'>
                    <Form.Label>
                        Email
                    </Form.Label>
                    <Form.Control type="email" />
                    </div>
                    <div className='col-lg-6 col-sm-12 mt-2'>
                    <Form.Label>
                        Phone
                    </Form.Label>
                    <Form.Control type="" />
                    </div>
                    </div>
                    <Button type="submit" className='my-4'>Submit</Button>
            </Form>
        </Container>
    )
}

export default ArrangeTestDrive;
