import React,  { useState } from "react";
import { Button, Container } from "react-bootstrap";
import CarModels from "../components/ViewAllCarComponent/CarModels";
import { allCar } from "../components/ModelOverview/data";
import Filter from "../components/FilterModel/Filter";
const ViewAllCar = () => {

    const [allCars, setAllCars] = useState(allCar);

    const [ filter, activeFilter] = useState(false);
    return (
        <Container className="my-5">
            <h2>View all models</h2>
            <Button variant="outline-dark" size="lg" className="mt-3" onClick={() => activeFilter(!filter)}>{filter ? "Close Filter" : "Open Filter"}</Button>
            {
                filter ? <Filter setAllCars={setAllCars} /> : null
            }
            <section className="row">
                {
                    allCars.map(( car ) => (
                    <CarModels car={car} />
                    )
                )
                }
            </section>
        </Container>
    )
}

export default ViewAllCar;