import React from 'react'
import { Container } from 'react-bootstrap';

function AboutUs() {
    return (
        <div>
                <div 
                style={{
                backgroundImage : 'url(https://www.audi.com/content/dam/gbp2/company/1920x600_company_desktop_stage.jpg?imwidth=1920&imdensity=1)',
                height: "350px",
                color: 'white',                       
                backgroundPosition: "center",
                backgroundSize: "cover"
                }}
                className='d-flex flex-column justify-content-center'
                >
                    <h2 className="text-center">Company</h2>
                </div>
                <Container className='my-5'>
                    <h4 className='text-center'>
                    The AUDI AG stands for sporty vehicles, high build quality and progressive design – for “Vorsprung durch Technik.” The Audi Group is among the world’s leading producers of premium cars.
                    To play an instrumental role in shaping the transformation as we head into a new age of mobility the Company is implementing its strategy step by step.
                    </h4>
                </Container>
                <div 
                style={{
                backgroundImage : 'url(https://www.audi.com/content/dam/gbp2/experience-audi/models-and-technology/concept-cars/aicon/1920x600-desktop-video-concept-cars-aicon.jpg?imwidth=1920&imdensity=1)',
                height: "350px",
                color: 'white',                       
                backgroundPosition: "center",
                backgroundSize: "cover"
                }}
                className='d-flex flex-column justify-content-center  mb-4'
                >
                    <h2 className="text-center mt-4 mb-5">Everything at a glance</h2>
                    <div className='d-flex flex-row justify-content-around align-items-center text-center'>
                        <div>
                            <p>over</p>
                            <h2>100</h2>
                            <h5>Markets</h5>
                        </div>
                        <div>
                            <p>worldwide</p>
                            <h2>~ 87,000</h2>
                            <h5>Employees</h5>
                        </div>
                        <div>
                            <h2>1,692,773</h2>
                            <h5>Deliveries in 2020</h5>
                        </div>
                        </div>
                </div>
        </div>
    )
}

export default AboutUs;